import axios from 'axios';
import { message } from 'antd';

// chrome same-sitecookie
// export const HOST = '/local-server';
export const HOST = 'http://101.43.119.45:7001';

export const FILES_HOST = 'http://101.43.119.45:7051'; // -,
// export const FILES_HOST = 'http://www.y-droid.com/fileSystem'; // -

axios.interceptors.response.use(
  null,
  error => {
    console.log(error);
    return Promise.reject(error.response);
  });

export default function fetch(option = {}) {
  const { url, byteResponse = false, ...rest } = option;
  return axios({
    url: `${HOST}${url}`,
    withCredentials: true,
    ...rest,
  }).then(res => {
    const { status, data } = res;
    if (status !== 200) {
      message.error('');
      return Promise.reject(new Error(''));
    }
    const { code, msg } = data || {};
    if (code === '1000') {
      return data.data;
    }
    if (byteResponse) {
      return data;
    }
    const errorMsg = message.error({
      content: msg || '',
      duration: 5,
      onClick: () => errorMsg() });
    throw new Error(msg);
    // return Promise.reject(new Error(msg));
  });
}
